import YtShare from "./posts/2022-07-02-YtShare";

const Post = () => {
  const param = useParams();
  let page = Object;
  // So far, this is only info object.
  if (param.id === "ytshare") page = YtShare;
  else if (param.id === "wd2uk") page = Wordl;
  return (
    <>
      <Head page={page} />
      <div className="single">
        <div className="article">
          <div className="thumb flex justify-center">
            <img src={page.thumb} alt="thumb" />
          </div>
          <h1 className="header">{page.title}</h1>
          <div className="mt-[20px]">
            <i>{page.date} 작성</i>{" "}
          </div>
          <div className="content-panel content leading-8">{page.story}</div>
        </div>
      </div>
      <div className="content-panel feedback">
        I ❤️ feedback.
        <br />
        Let me know what you think of this article on twitter{" "}
        <a href="https://twitter.com/YG1ee">@YG1ee</a>!
      </div>

      <br />
      <footer>
        <small>
          Theme originally made with ❤️ by{" "}
          <a href="https://twitter.com/_jacobtomlinson">Jacob Tomlinson</a>
        </small>
        <br />
        <small>
          &lt;/&gt; on &nbsp;
          <a href="https://github.com/jacobtomlinson/carte-noire">
            Github
          </a>{" "}
          &nbsp;
          <i className="fab fa-github-alt" />
        </small>
      </footer>
    </>
  );
};

export default Post;
