import { useState } from "react";
import Head from "../../blog/Head";
import PageInfo from "../../blog/posts/2022-07-02-YtShare";

let shareLink = "";

export default function YtShare() {
  const [input_phase, setPhase] = useState(true);

  const [NG, setFail] = useState(false);
  const [result_link, setResLink] = useState("");
  const [result_msg, setRes] = useState("");

  const shareYT = ({ input_link, use_ts, hrs, min, sec }) => {
    const case_str = ["?v=", "&v=", "shorts/", "youtu.be/"];
    let linkPos = -1;
    let timestamp = 0;

    if (
      use_ts &&
      !(hrs === 0 && min === 0 && sec === 0) &&
      !(hrs < 0 || min < 0 || sec < 0)
    )
      timestamp = hrs * 3600 + min * 60 + sec;

    if (result_link.length > 0) {
      shareLink = result_link;
      linkPos = 0;
    } else {
      let itr = 0;
      for (; itr < case_str.length; itr++) {
        linkPos = input_link.indexOf(case_str[itr]);
        if (linkPos > -1) break;
      }

      if (linkPos > -1) {
        const startIdx = linkPos + case_str[itr].length;
        const endIdx = startIdx + 11; // 11 being youtube link ID length
        shareLink = "https://youtu.be/" + input_link.slice(startIdx, endIdx);
      }
    }
    if (linkPos > -1) {
      if (use_ts && parseInt(timestamp, 10) !== 0)
        shareLink += "&t=" + timestamp;
      // const linkObj = { text: shareLink };
      // if (!navigator.canShare) {
      navigator.clipboard.writeText(shareLink);

      setRes("클립보드에 복사 완료 😎");
      setResLink(shareLink);
      // }
      // else if (navigator.canShare(linkObj)) {
      //   navigator
      //     .share(linkObj)
      //     .then(() => {
      //       setRes("공유 완료 😎");
      //     })
      //     .catch(() => {
      //       setRes("공유 취소 😲");
      //     });
      //   setResLink(shareLink);
      // }
      // else setRes("예기치 않은 오류 😥");
    } else {
      setFail(true);
      if (input_link.search("youtu.be") !== -1) {
        setRes("이미 변환이 완료된 링크네요 😅");
        setResLink(shareLink);
      } else setRes("유튜브 영상 링크가 아닌 것 같습니다 🙄");
    }
    setPhase(false);
  };

  const retryYT = () => {
    setPhase(true);
    setResLink("");
    setFail(false);
  };

  const RetryButton = (
    <input
      type="button"
      onClick={retryYT}
      className="px-[4px] text-sm text-black font-['IBM Plex Sans KR', 'Bahnschrift']  bg-[#FF79C6] border-2 border-[#FF79C6] rounded-[5px]"
      value="다시하기"
    />
  );

  const ShareUI = () => {
    const [input_link, setInput] = useState("");
    const [hrs, setHour] = useState("");
    const [min, setMin] = useState("");
    const [sec, setSec] = useState("");
    const [use_ts, setTS] = useState(false);

    return (
      <>
        <br />
        <h3 className="yellow font-bold mb-[10px]">유튜브 링크</h3>

        <form
          onSubmit={(e) => {
            e.preventDefault();
            shareYT({ input_link, use_ts, hrs, min, sec });
          }}
        >
          {input_phase ? (
            <>
              <p>
                <input
                  type="text"
                  onChange={(e) => {
                    setInput(e.target.value);
                  }}
                  placeholder="공유할 유튜브 링크"
                  className="p-[5px] text-sm text-[#F8F8F2] w-[280px] bg-[#44475A] rounded-[5px] mb-[20px]"
                />
              </p>
              <h4 className="pink font-bold mb-[10px]">
                타임스탬프 사용&nbsp;&nbsp;
                <input
                  type="checkbox"
                  id="use_ts"
                  onChange={(e) => {
                    setTS(e.target.checked);
                  }}
                />
              </h4>
              <p>
                {use_ts ? (
                  <div className="mb-[20px]">
                    <input
                      type="number"
                      min={0}
                      max={999}
                      value={hrs}
                      onChange={(e) => setHour(parseInt(e.target.value, 10))}
                      className="w-[80px] text-black"
                      placeholder="시"
                    />
                    &nbsp;
                    <input
                      type="number"
                      min={0}
                      max={59}
                      value={min}
                      onChange={(e) => setMin(parseInt(e.target.value, 10))}
                      className="w-[80px] text-black"
                      placeholder="분"
                    />
                    &nbsp;
                    <input
                      type="number"
                      min={0}
                      max={59}
                      value={sec}
                      onChange={(e) => setSec(parseInt(e.target.value, 10))}
                      className="w-[80px] text-black"
                      placeholder="초"
                    />
                  </div>
                ) : (
                  ""
                )}
              </p>
            </>
          ) : (
            <>
              <p className="mb-[10px]">{result_link}</p>
              <p className="mb-[10px]">{result_msg}</p>
            </>
          )}
          {!NG ? (
            <input
              type="submit"
              className="px-[4px] text-sm text-black font-['IBM Plex Sans KR', 'Bahnschrift']  bg-[#50FA7B] border-2 border-[#50FA7B] rounded-[5px]"
              value="공유하기"
            />
          ) : (
            ""
          )}
          {!input_phase && !NG ? <>&emsp;</> : ""}
          {!input_phase ? RetryButton : ""}
        </form>
      </>
    );
  };

  return (
    <>
      <Head page={PageInfo} />
      <div className="m-[10px]">
        <div className="flex justify-center">
          <img
            src="/res/ytshare/ytShare_w.png"
            width="200"
            height="200"
            alt="ytShare logo"
          />
        </div>
        <div className="purple">
          <b>
            모바일에서도 타임스탬프를 추가해서
            <br />
            유튜브 링크를 편하게 공유해보세요
          </b>
        </div>
        <ShareUI />
      </div>
    </>
  );
}
