import { useState } from "react";
import Head from "../../blog/Head";
import PageInfo from "../../blog/posts/2022-06-23-wd2uk";

export default function Wordl() {
  const [IO, setIO] = useState(false);
  const [fail, setFail] = useState(true);
  const [output_result, setOutput] = useState("");
  const [state_msg, setMsg] = useState("");
  let result = "";

  const printUK = () => {
    setIO(true);
    const wordl = document.querySelector("#wd_input").value;
    if (wordl.length !== 0) {
      let wordlFlag = false; // Wordle이 맞는지 여부
      let type = 0b00; // type이 혼용되었는지 bitmask

      for (let i = 0; i < wordl.length; i++) {
        // 색 있는 사각형 (이모지)
        if (wordl[i] === "\uD83D") {
          switch (wordl[i + 1]) {
            // wrong spot
            case "\uDFE6": // 🟦
            case "\uDFE8": // 🟨
              result += "🟨";
              wordlFlag = true;
              break;
            // correct spot
            case "\uDFE7": // 🟧
            case "\uDFE9": // 🟩
              result += "🟦";
              wordlFlag = true;
              break;
            default:
              result += wordl[i];
              continue;
          }

          // type check
          switch (wordl[i + 1]) {
            case "\uDFE6": // 🟦
            case "\uDFE7": // 🟧
              type |= 0b10;
              break;
            case "\uDFE8": // 🟨
            case "\uDFE9": // 🟩
              type |= 0b01;
              break;
            default:
              result += wordl[i];
              continue;
          }
          // 이모지는 2자로 이루어짐.
          i++;
        }
        // not in any spot
        // 빈 사각형과 검은 사각형은 이모지가 아님.
        else if (wordl[i] === "⬜" || wordl[i] === "⬛") {
          result += "⬛";
          wordlFlag = true;
        }
        // plain character
        else result += wordl[i];
      }

      if (wordlFlag && type !== 0b11) {
        setFail(false);
        setOutput(result);
      } else {
        setOutput("Wordle의 결과가 아닌 것 같습니다 :(\n");
      }
    } else {
      setOutput("값을 입력해주세요 :(");
    }
  };

  const retryUK = () => {
    setIO(false);
    setFail(true);
    setMsg("");
  };

  const shareUK = () => {
    // if (navigator.share) {
    //   navigator
    //     .share({
    //       text: output_result,
    //     })
    //     .then(() => setMsg("공유 완료 😎"))
    //     .catch((err) => setMsg("공유 취소 😲"));
    // } else {
    navigator.clipboard.writeText(output_result);
    setMsg("클립보드에 복사 완료 😎");
    // }
  };

  const InputRes = () => (
    <>
      <h1>
        Wordle 결과를
        <br />
        우크라이나 국기
        <br />
        <img
          src="/res/wd2uk/favicon.ico"
          alt="UK flag"
          width={32}
          className="inline"
        />{" "}
        색으로 변환
      </h1>
      <textarea
        autoFocus
        required
        id="wd_input"
        rows="8"
        cols="20"
        placeholder="Wordle 결과를 붙여넣고&#10;버튼을 누르세요."
        style={{
          padding: 5,
          borderRadius: 5,
          backgroundColor: "#44475A",
          color: "#F8F8F2",
          fontFamily: "'IBM Plex Sans KR', 'Bahnschrift'",
        }}
        className="mt-[10px]"
      />
      <br />
      <button
        style={{
          backgroundColor: "#50FA7B",
          border: "solid 2px #50FA7B",
          borderRadius: 5,
          fontFamily: "'IBM Plex Sans KR', 'Bahnschrift'",
        }}
        onClick={printUK}
        className="px-[2px] text-black"
      >
        변환하기
      </button>
    </>
  );

  const OutputRes = () => (
    <>
      <div>{output_result}</div>
      <br />
      {state_msg.length !== 0 ? (
        <>
          <div>{state_msg}</div>
          <br />
        </>
      ) : null}
      {!fail ? (
        <>
          <input
            type="button"
            style={{
              backgroundColor: "#50FA7B",
              border: "solid 2px #50FA7B",
              borderRadius: 5,
              fontFamily: "'IBM Plex Sans KR', 'Bahnschrift'",
            }}
            onClick={shareUK}
            value="공유하기"
          />
          &emsp;
        </>
      ) : null}
      <input
        type="button"
        style={{
          backgroundColor: "#FF79C6",
          border: "solid 2px #FF79C6",
          borderRadius: 5,
          fontFamily: "'IBM Plex Sans KR', 'Bahnschrift'",
        }}
        onClick={retryUK}
        value="다시하기"
      />
    </>
  );

  return (
    <>
      <Head page={PageInfo} />
      <div className="App">{!IO ? <InputRes /> : <OutputRes />}</div>
    </>
  );
}
